import { type } from '../../../util/util';
import { Action } from '@ngrx/store';

export const ActionTypes = {
    SET_PRESTINE: type<'CONTAINER_ROUTEDETAIL_SET_PRISTINE'>('CONTAINER_ROUTEDETAIL_SET_PRISTINE'),
    CLEAR_PRESTINE: type<'CONTAINER_ROUTEDETAIL_CLEAR_PRISTINE'>('CONTAINER_ROUTEDETAIL_CLEAR_PRISTINE'),
};

export class SetRouteDetailPristine implements Action {
    type = ActionTypes.SET_PRESTINE;

    public constructor() {
    }
}

export class ClearRouteDetailPristine implements Action {
    type = ActionTypes.CLEAR_PRESTINE;

    public constructor() {
    }
}

export type Actions = SetRouteDetailPristine | ClearRouteDetailPristine;



