import * as route from '../../actions/data/route';
import { Route } from '../../../trainnetwork/entities/Route';

export function routesReducer(state: Array<Route> = [],
  action: route.Actions): Array<Route> {
  switch (action.type) {
    case route.ActionTypes.ROUTES_ADD:
      return [...state, action.payload.route];
    case route.ActionTypes.ROUTES_SET_ALL:
      return [...action.payload.routes];
    case route.ActionTypes.ROUTES_REMOVE:
      return state.filter(item => item.id !== action.payload.id);
    case route.ActionTypes.ROUTES_UPDATE:
      return state.map(item => item.id === action.payload.id ? Object.assign({}, action.payload.route) : item);
    default:
      return state;
  }
};



