import { Injectable, OnDestroy } from '@angular/core';
import { Store } from '@ngrx/store';
import { ApplicationState } from '../../statemanagement/state/ApplicationState';
import { ResetStore } from '../../statemanagement/metareducers/reset-reducer';
import { RouteUrls } from '../RouteUrls';
import {
    SetAllRoutes,
    RemoveRoute,
    AddRoute,
    UpdateRoute,
    AddFetchedRoute,
    ClearFetchedRoute
} from '../../statemanagement/actions/data/route';
import { go, back } from '@ngrx/router-store';
import { Route } from '../../trainnetwork/entities/Route';
import { RoutesService } from '../../trainnetwork/services/routes.service';
import { OperatorsService } from '../../trainnetwork/services/operators.service';
import { NotificationService } from '../services/notification.service';
import { SetRouteDetailPristine, ClearRouteDetailPristine } from '../../statemanagement/actions/containers/route-detail';
import { unsavedChangesMessage } from '../messages';
import { LocalStorageService } from '../services/localStorage.service';
import {
    SetAllOperators,
    RemoveOperator,
    AddOperator,
    UpdateOperator,
    AddFetchedOperator,
    ClearFetchedOperator
} from '../../statemanagement/actions/data/operator';
import { Operator } from '../../trainnetwork/entities/Operator';
import { SetOperatorDetailPristine, ClearOperatorDetailPristine } from '../../statemanagement/actions/containers/operator-detail';
import { Observable } from 'rxjs/Observable';
import { RouteModel } from '../../trainnetwork/model/RouteModel';
import { environment } from '../../environments/environment';

declare var __insp: any;

@Injectable()
export class ShellSandbox implements OnDestroy {
    isAuthenticated$ = this.store.select(state => state.data.authentication.isAuthenticated);
    routes$ = this.store.select(state => state.data.routes);
    route$ = this.store.select(state => state.data.route);
    routeDetailPristine$ = this.store.select(state => state.containers.routeDetailPristine);
    operators$ = this.store.select(state => state.data.operators);
    operator$ = this.store.select(state => state.data.operator);
    operatorDetailPristine$ = this.store.select(state => state.containers.operatorDetailPristine);
    email: string;
    private routeDetailPristine: boolean;
    private routeDetailPristineSubscription: any;
    private operatorDetailPristine: boolean;
    private operatorDetailPristineSubscription: any;

    constructor(
        private store: Store<ApplicationState>,
        private routesService: RoutesService,
        private operatorsService: OperatorsService,
        private notificationService: NotificationService,
        private localStorageService: LocalStorageService) {
        this.routeDetailPristineSubscription = this.routeDetailPristine$.subscribe(pristine =>
            this.routeDetailPristine = pristine.isPristine);
        this.operatorDetailPristineSubscription = this.operatorDetailPristine$.subscribe(pristine =>
            this.operatorDetailPristine = pristine.isPristine);
        this.email = this.getEmail(this.localStorageService.token);
        __insp.push(['identify', this.email]);
        __insp.push(['tagSession', {
            email: this.email,
            environmentIsHmr: environment.hmr,
            environmentIsProduction: environment.production
        }]);
    }

    private getEmail(token): string {
        return atob(token).split(':')[0];
    }

    ngOnDestroy() {
        this.routeDetailPristineSubscription.unsubscribe();
        this.operatorDetailPristineSubscription.unsubscribe();
    }

    logout(): void {
        if (!this.routeDetailPristine || !this.operatorDetailPristine) {
            if (!confirm(unsavedChangesMessage)) {
                return;
            }
        }
        this.store.dispatch(new ResetStore());
        this.localStorageService.clear();
        this.gotoMap();
    }

    // Route methods

    loadRoutes() {
        this.routesService.fetchAll().subscribe((routes) => {
            this.store.dispatch(new SetAllRoutes(routes));
        },
            (err) => this.notificationService.show('An error occured when loading the routes.'));
    }

    removeRoute(routeId: string) {
        const routeSubscription = this.routes$.subscribe(routes => {
            routes.forEach(route => {
                const routeModel = new RouteModel(route);
                if (routeModel.viaIds) {
                    const impactedIndex = routeModel.viaIds.findIndex(viaId => viaId === routeId);
                    if (impactedIndex > -1) {
                        routeModel.viaIds = routeModel.viaIds.filter(viaId => viaId !== routeId);
                        this.addOrUpdateRoute(routeModel.toRoute(), true);
                    }
                }
            });
            this.routesService.remove(routeId).subscribe(response => {
                this.store.dispatch(new RemoveRoute(routeId));
                this.notificationService.show('Route was removed.');
            },
                (err) => this.notificationService.show('An error occured when removing the route.'));
        });
        this.loadRoutes();
        routeSubscription.unsubscribe();
    }

    private onRouteSavedSuccessfully(message: string, noRedirect: boolean) {
        this.setRouteDetailPristine();
        this.notificationService.show(message);
        if (!noRedirect) {
            this.gotoRoutes();
        }
    }

    addOrUpdateRoute(route: Route, noRedirect?: boolean) {
        if (!route.id) {
            this.routesService.add(route).subscribe(response => {
                this.store.dispatch(new AddRoute(new Route(
                    response.text(),
                    route.viaIds,
                    route.terminal,
                    route.type
                )));
                this.onRouteSavedSuccessfully(`Route "${route.terminal.name}" was added.`, noRedirect);
            },
                (err) => this.notificationService.show(`An error occured when adding route "${route.terminal.name}".`));
        } else {
            this.routesService.update(route).subscribe(response => {
                this.store.dispatch(new UpdateRoute(route.id, route));
                this.onRouteSavedSuccessfully(`Route "${route.terminal.name}" was updated.`, noRedirect);
            },
                (err) => this.notificationService.show(`An error occured when updating route "${route.terminal.name}".`));
        }
    }

    fetchRoute(routeId: string) {
        this.routesService.fetch(routeId).subscribe(route => this.store.dispatch(new AddFetchedRoute(route)),
            (err) => this.notificationService.show('An error occured when fetching the route.'));
    }

    clearFetchedRoute() {
        this.store.dispatch(new ClearFetchedRoute());
    }

    setRouteDetailPristine() {
        this.store.dispatch(new SetRouteDetailPristine());
    }

    clearRouteDetailPristine() {
        this.store.dispatch(new ClearRouteDetailPristine());
    }

    // Operator methods

    loadOperators() {
        this.operatorsService.fetchAll().subscribe((operators) => {
            this.store.dispatch(new SetAllOperators(operators));
        },
            (err) => this.notificationService.show('An error occured when loading the operators.'));
    }

    removeOperator(operatorId: string) {
        const routeSubscription = this.routes$.subscribe(routes => {
            routes.forEach(route => {
                const routeModel = new RouteModel(route);
                if (routeModel.timings) {
                    const impactedTimings = routeModel.timings.filter(timing => timing.operatorId === operatorId);
                    if (impactedTimings.length > 0) {
                        impactedTimings.forEach(timing => {
                            const index = routeModel.timings.indexOf(timing);
                            routeModel.timings.splice(index, 1);
                        });
                        this.addOrUpdateRoute(routeModel.toRoute(), true);
                    }
                }
            });
            this.operatorsService.remove(operatorId).subscribe(response => {
                this.store.dispatch(new RemoveOperator(operatorId));
                this.notificationService.show('Operator was removed.');
            },
                (err) => this.notificationService.show('An error occured when removing the operator.'));
        });
        this.loadRoutes();
        routeSubscription.unsubscribe();
    }

    private onOperatorSavedSuccessfully(message: string, noRedirect?: boolean) {
        this.setOperatorDetailPristine();
        this.notificationService.show(message);
        if (!noRedirect) {
            this.gotoOperators();
        }
    }

    addOrUpdateOperator(operator: Operator, noRedirect?: boolean) {
        if (!operator.id) {
            this.operatorsService.add(operator).subscribe(response => {
                this.store.dispatch(new AddOperator(new Operator(
                    response.text(),
                    operator.name,
                    operator.url,
                    operator.comments
                )));
                this.onOperatorSavedSuccessfully(`Operator "${operator.name}" was added.`, noRedirect);
            },
                (err) => this.notificationService.show(`An error occured when adding operator "${operator.name}".`));
        } else {
            this.operatorsService.update(operator).subscribe(response => {
                this.store.dispatch(new UpdateOperator(operator.id, operator));
                this.onOperatorSavedSuccessfully(`Operator "${operator.name}" was updated.`, noRedirect);
            },
                (err) => this.notificationService.show(`An error occured when updating operator "${operator.name}".`));
        }
    }

    fetchOperator(operatorId: string) {
        this.operatorsService.fetch(operatorId).subscribe(operator => this.store.dispatch(new AddFetchedOperator(operator)),
            (err) => this.notificationService.show('An error occured when fetching the operator.'));
    }

    clearFetchedOperator() {
        this.store.dispatch(new ClearFetchedOperator());
    }

    setOperatorDetailPristine() {
        this.store.dispatch(new SetOperatorDetailPristine());
    }

    clearOperatorDetailPristine() {
        this.store.dispatch(new ClearOperatorDetailPristine());
    }

    // Map routes

    gotoMap() {
        this.store.dispatch(go(RouteUrls.URL_MAP));
    }

    // Route routes

    gotoRoutes() {
        this.store.dispatch(go(RouteUrls.URL_ROUTES));
    }

    gotoEditRoute(routeId: string) {
        this.store.dispatch(go(RouteUrls.URL_ROUTES + '/' + routeId));
    }

    gotoAddRoute() {
        this.store.dispatch(go(RouteUrls.URL_ROUTE_ADD));
    }

    // Operator operators

    gotoOperators() {
        this.store.dispatch(go(RouteUrls.URL_OPERATORS));
    }

    gotoEditOperator(operatorId: string) {
        this.store.dispatch(go(RouteUrls.URL_OPERATORS + '/' + operatorId));
    }

    gotoAddOperator() {
        this.store.dispatch(go(RouteUrls.URL_OPERATOR_ADD));
    }
}



