import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { ApplicationState } from '../../statemanagement/state/ApplicationState';
import { Login } from '../../statemanagement/actions/data/authentication';
import { NotificationService } from '../services/notification.service';
import { TokenData } from '../../trainnetwork/entities/TokenData';
import { LocalStorageService } from '../services/localStorage.service';
import { AuthenticationService } from '../../trainnetwork/services/authentication.service';

@Injectable()
export class LoginSandbox {
    isAuthenticated$ = this.store.select(state => state.data.authentication.isAuthenticated);
    routes$ = this.store.select(state => state.data.routes);

    constructor(
        private store: Store<ApplicationState>,
        private authenticationService: AuthenticationService,
        private notificationService: NotificationService,
        private localStorageService: LocalStorageService) {
        if (this.localStorageService.token !== null) {
            this.loginWithToken(this.localStorageService.token);
        }
    }

    login(email: string, password: string): void {
        this.loginWithToken(btoa(`${email}:${password}`));
    }

    private loginWithToken(token: string) {
        const tokenData = new TokenData(token);
        this.authenticationService.login(tokenData).subscribe(loggedIn => {
            if (loggedIn) {
                this.store.dispatch(new Login());
            } else {
                this.notificationService.show('Login failed');
            }
        },
            err => this.notificationService.show('Login failed')
        );
    }
}



