import { MdSnackBar } from '@angular/material';
import { Injectable } from '@angular/core';

@Injectable()
export class NotificationService {
  constructor(private snackBar: MdSnackBar) {
  }

  show(message: string) {
    this.snackBar.open(message, null, { duration: 3000 });
  }
}



