import { TimingData } from '../entities/TimingData';

export class TimingDataModel {
    public departure: string = null;
    public closing: string = null;
    public pickup: string = null;
    public profile: string = null;

    constructor(timingData?: TimingData) {
        if (timingData) {
            this.departure = timingData.departure;
            this.closing = timingData.closing;
            this.pickup = timingData.pickup;
            this.profile = timingData.profile;
        }
    }
}


