import { Component, OnInit, Input, ChangeDetectionStrategy, EventEmitter, Output } from '@angular/core';
import { Operator } from '../../../trainnetwork/entities/Operator';
import { OperatorModel } from '../../../trainnetwork/model/OperatorModel';
import { FormControl, Validators, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-operator-detail',
  templateUrl: './operator-detail.component.html',
  changeDetection: ChangeDetectionStrategy.Default
})
export class OperatorDetailComponent implements OnInit {
  operatorModel: OperatorModel;
  availableOperators: Operator[];
  intitialOperatorModelJson: string;

  detailFormGroup: FormGroup;

  nameFormControl = new FormControl('', [Validators.required]);
  urlFormControl = new FormControl();
  commentsFormControl = new FormControl();

  private isPristine = true;
  private _operators: Operator[];

  @Input()
  public set operators(value: Operator[]) {
    this._operators = value;
    this.setAvailableOperators();
  }

  @Input()
  public set operator(value: Operator) {
    this.operatorModel = new OperatorModel(value);
    this.setAvailableOperators();
    this.intitialOperatorModelJson = JSON.stringify(this.operatorModel);
  }

  @Output()
  cancel: EventEmitter<any> = new EventEmitter;

  @Output()
  save: EventEmitter<Operator> = new EventEmitter;

  @Output()
  pristine: EventEmitter<boolean> = new EventEmitter;

  private setAvailableOperators() {
    if (this.operatorModel && this._operators) {
      this.availableOperators = this._operators.filter(r => r.id !== this.operatorModel.id);
    }
  }

  ngOnInit() {
    this.initializeFormGroup();
    this.initializeCheckModel();
  }

  private initializeCheckModel() {
    const changeSignaler = (value: any) => this.checkModel();

    this.nameFormControl.valueChanges.subscribe(changeSignaler);
    this.urlFormControl.valueChanges.subscribe(changeSignaler);
    this.commentsFormControl.valueChanges.subscribe(changeSignaler);
  }

  private initializeFormGroup() {
    this.detailFormGroup = new FormGroup({});
    this.detailFormGroup.addControl('name', this.nameFormControl);
    this.detailFormGroup.addControl('url', this.urlFormControl);
    this.detailFormGroup.addControl('comments', this.commentsFormControl);
  }

  getTitle(operatorModel: OperatorModel): string {
    if (operatorModel.id) {
      return 'Edit operator';
    }
    return 'Add operator';
  }

  cancelDetail() {
    this.cancel.emit(null);
  }

  saveDetail() {
    this.save.emit(this.operatorModel.toOperator());
  }

  checkModel() {
    const currentPristine = JSON.stringify(this.operatorModel) === this.intitialOperatorModelJson;
    if (this.isPristine !== currentPristine) {
      this.isPristine = currentPristine;
      this.pristine.emit(currentPristine);
    }
  }
}



