import { TimingModel } from '../../trainnetwork/model/TimingModel';
import * as _ from 'lodash';

export class TimingDialogModel {
    public readonly originalTimingModel: TimingModel;
    public readonly modifiedTimingModel: TimingModel;

    constructor(public terminalName, public operatorName: string, public add: boolean, timing: TimingModel) {
        this.originalTimingModel = _.cloneDeep(timing);
        this.modifiedTimingModel = _.cloneDeep(timing);
    }
}


