import { Component, OnInit, Inject } from '@angular/core';
import { TimingDialogModel } from '../TimingDialogModel';
import { MD_DIALOG_DATA, MdDialogRef } from '@angular/material';

@Component({
  selector: 'app-timing-dialog',
  templateUrl: './timing-dialog.component.html',
})
export class TimingDialogComponent implements OnInit {

  constructor(public dialogRef: MdDialogRef<TimingDialogComponent>, @Inject(MD_DIALOG_DATA) public data: TimingDialogModel) { }

  ngOnInit() {
  }

  okClicked() {
    this.dialogRef.close(this.data);
  }

  cancelClicked() {
      this.dialogRef.close();
  }
}



