import * as routeDetailPristine from '../../actions/containers/route-detail';
import { PristineContainerState } from '../../state/ContainersState';

const initialState: PristineContainerState = {
    isPristine: true
};

export function routeDetailPristineReducer(state: PristineContainerState = initialState,
    action: routeDetailPristine.Actions): PristineContainerState {
    switch (action.type) {
        case routeDetailPristine.ActionTypes.CLEAR_PRESTINE:
            return {
                isPristine: false
            };
        case routeDetailPristine.ActionTypes.SET_PRESTINE:
            return {
                isPristine: true
            };
        default:
            return state;
    }
};



