import { LatLngBoundsLiteral } from '@agm/core';
import { Line } from './Line';
import { Location } from './Location';
import { Route } from '../../../trainnetwork/entities/Route';

export class MapTransformationHelper {
    private static readonly redIcon = 'http://maps.google.com/mapfiles/ms/icons/red-dot.png';
    private static readonly purpleIcon = 'http://maps.google.com/mapfiles/ms/icons/purple-dot.png';
    private static readonly blueIcon = 'http://maps.google.com/mapfiles/ms/icons/blue-dot.png';

    private static readonly combinantLocation = new Location(
        51.37123953903346,
        4.288451224565506,
        null,
        'Combinant',
        MapTransformationHelper.redIcon);

    static getLocations(routes: Route[]): Location[] {
        const locations = routes.map(route =>
            new Location(
                route.terminal.coordinates.latitude,
                route.terminal.coordinates.longitude,
                route.timings,
                route.terminal.name,
                route.viaIds !== null && route.viaIds.length > 0 ? this.blueIcon : this.purpleIcon));
        return [this.combinantLocation, ...locations];
    }

    static getBounds(locations: Location[]): LatLngBoundsLiteral {
        const bounds = <LatLngBoundsLiteral>{
            north: this.combinantLocation.latitude,
            east: this.combinantLocation.longitude,
            south: this.combinantLocation.latitude,
            west: this.combinantLocation.longitude
        };
        locations.map(curr => {
            if (curr.latitude > bounds.north) {
                bounds.north = curr.latitude;
            }
            if (curr.latitude < bounds.south) {
                bounds.south = curr.latitude;
            }
            if (curr.longitude > bounds.east) {
                bounds.east = curr.longitude;
            }
            if (curr.longitude < bounds.west) {
                bounds.west = curr.longitude;
            }
        });
        return bounds;
    }

    static getLines(routes: Route[]): Line[] {
        const lines: Line[] = [];
        routes.map(route => {
            let from: Location = this.combinantLocation;
            if (route.viaIds !== null && route.viaIds.length > 0) {
                route.viaIds.forEach((item, index) => {
                    if (item) {
                        const fromCoordinates = routes.find(r => r.id === item).terminal.coordinates;
                        from = new Location(fromCoordinates.latitude, fromCoordinates.longitude);
                        lines.push(
                            new Line(
                                from,
                                new Location(route.terminal.coordinates.latitude, route.terminal.coordinates.longitude),
                                '#0000FF'
                            )
                        );
                    }
                });
            } else {
                lines.push(
                    new Line(
                        from,
                        new Location(route.terminal.coordinates.latitude, route.terminal.coordinates.longitude),
                        '#FF0000'
                    )
                );
            }
        });
        return lines;
    }
}


