import { Route } from '../entities/Route';
import { TerminalModel } from './TerminalModel';
import { Terminal } from '../entities/Terminal';
import { Coordinates } from '../entities/Coordinates';
import { TimingModel } from './TimingModel';
import { Timing } from '../entities/Timing';
import { TimingData } from '../entities/TimingData';

export class RouteModel {
    public id: string;

    private _viaIds: string[];
    public set viaIds(value: string[]) {
        this._viaIds = value;
        if (this._viaIds !== null && this._viaIds.length > 0) {
            this.type = 'Indirect';
        } else {
            this.type = 'Direct';
        }
    }
    public get viaIds(): string[] {
        return this._viaIds;
    }

    public type: string;
    public terminal: TerminalModel;
    public timings: TimingModel[]

    constructor(route?: Route) {
        if (route) {
            this.id = route.id;
            this.viaIds = route.viaIds === null ? [] : route.viaIds;
            this.terminal = new TerminalModel(route.terminal);
            this.timings = route.timings === null ? [] : route.timings.map(t => new TimingModel(t));
        }
    }

    toRoute(): Route {
        return new Route(
            this.id,
            this.viaIds,
            new Terminal(
                this.terminal.name,
                new Coordinates(
                    this.terminal.coordinates.latitude,
                    this.terminal.coordinates.longitude
                )
            ),
            this.type,
            this.timings.map(t => new Timing(
                t.operatorId,
                t.routeId,
                new TimingData(
                    t.timingDataTo.departure,
                    t.timingDataTo.closing,
                    t.timingDataTo.pickup,
                    t.timingDataTo.profile
                ),
                new TimingData(
                    t.timingDataFrom.departure,
                    t.timingDataFrom.closing,
                    t.timingDataFrom.pickup,
                    t.timingDataFrom.profile
                ))
            )
        );
    }
}



