import * as operator from '../../actions/data/operator';
import { Operator } from '../../../trainnetwork/entities/Operator';

export function operatorReducer(state: Operator = new Operator(),
  action: operator.Actions): Operator {
  switch (action.type) {
    case operator.ActionTypes.OPERATOR_ADD_FETCHED:
      return Object.assign({}, action.payload.operator);
    case operator.ActionTypes.OPERATOR_CLEAR_FETCHED:
      return new Operator();
    default:
      return state;
  }
};



