import { Timing } from '../entities/Timing';
import { TimingDataModel } from './TimingDataModel';

export class TimingModel {
    public operatorId: string;
    public routeId: string;
    public timingDataTo: TimingDataModel = new TimingDataModel();
    public timingDataFrom: TimingDataModel = new TimingDataModel();

    constructor(timing?: Timing) {
        if (timing) {
            this.operatorId = timing.operatorId;
            this.routeId = timing.routeId;
            this.timingDataTo = new TimingDataModel(timing.timingDataTo);
            this.timingDataFrom = new TimingDataModel(timing.timingDataFrom);
        }
    }
}


