import { Injectable, OnDestroy } from '@angular/core';
import { CanDeactivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { RouteDetailComponent } from '../routes/route-detail/route-detail.component';
import { Observable } from 'rxjs/Observable';
import { ShellSandbox } from '../shell/shell.sandbox';
import { unsavedChangesMessage } from '../messages';

@Injectable()
export class CancelRouteDetailGuard implements CanDeactivate<RouteDetailComponent>, OnDestroy {
    private isRouteDetailPrisitine: boolean;
    private isRouteDetailPristineSubscription: any;

    constructor(private sb: ShellSandbox) {
        sb.routeDetailPristine$.subscribe(pristine => this.isRouteDetailPrisitine = pristine.isPristine);
    }

    ngOnDestroy() {
        this.isRouteDetailPristineSubscription.unsubscribe();
    }

    canDeactivate(
        component: RouteDetailComponent,
        currentRoute: ActivatedRouteSnapshot,
        currentState: RouterStateSnapshot,
        nextState: RouterStateSnapshot
    ): Observable<boolean> | Promise<boolean> | boolean {
        if (!this.isRouteDetailPrisitine) {
            const result = confirm(unsavedChangesMessage);
            if (result) {
                this.sb.setRouteDetailPristine();
            }
            return result;
        }
        return true;
    }
}



