import { Component, Input, ChangeDetectionStrategy, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { Route } from '../../../trainnetwork/entities/Route';
import { ArrayDataSource } from '../../ArrayDataSource';
import { RemoveRoute } from '../../../statemanagement/actions/data/route';

@Component({
  selector: 'app-routes-list',
  templateUrl: './routes-list.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RoutesListComponent {

  displayedColumns = ['terminal', 'via', 'type', 'actions'];

  @Input()
  public set routes(value: Route[]) {
    this.dataSource.setData(value);
    this.triggerChangeDetection();
  }

  @Output()
  removeRoute: EventEmitter<Route> = new EventEmitter();

  @Output()
  editRoute: EventEmitter<Route> = new EventEmitter();

  dataSource: ArrayDataSource<Route> | null;

  constructor(private changeDetectorRef: ChangeDetectorRef) {
    this.dataSource = new ArrayDataSource<Route>();
  }

  getFromName(route: Route): string {
    if (route.type === 'Indirect') {
      return this.getViaName(route.viaIds);
    }
    return 'Combinant';
  }

  remove(route: Route) {
    this.removeRoute.emit(route);
  }

  edit(route: Route) {
    this.editRoute.emit(route);
  }

  private getViaName(viaIds) {
    const viaName = viaIds.reduce(
      (prev, curr, index) => {
        if (curr) {
        return prev += (prev.length === 0 ? '' : ', ') + this.dataSource.currentData.find(r => r.id === curr).terminal.name;
        } else {
          return prev;
        }
      },
      ''
    );
    return viaName;
  }

  // TODO: check out why this hack is needed
  private triggerChangeDetection() {
    setTimeout(() => this.changeDetectorRef.detectChanges(), 0);
    setTimeout(() => this.changeDetectorRef.detectChanges(), 0);
    setTimeout(() => this.changeDetectorRef.detectChanges(), 0);
  }
}



