import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { ShellSandbox } from '../../shell/shell.sandbox';
import { Route } from '../../../trainnetwork/entities/Route';

@Component({
  selector: 'app-routes',
  templateUrl: './routes-container.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RoutesContainerComponent implements OnInit {
  constructor(public sb: ShellSandbox) { }

  ngOnInit() {
    this.sb.loadRoutes();
  }

  removeRoute(route: Route) {
    if (confirm(`Are you sure you want to remove route "${route.terminal.name}"?`)) {
      this.sb.removeRoute(route.id);
    }
  }

  editRoute(route: Route) {
    this.sb.gotoEditRoute(route.id);
  }

  createOrUpdateRoute(route: Route) {
    this.sb.addOrUpdateRoute(route);
  }
}



