import { Component, Input, ChangeDetectionStrategy, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { Operator } from '../../../trainnetwork/entities/Operator';
import { ArrayDataSource } from '../../ArrayDataSource';
import { RemoveOperator } from '../../../statemanagement/actions/data/operator';

@Component({
  selector: 'app-operators-list',
  templateUrl: './operators-list.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OperatorsListComponent {

  displayedColumns = ['name', 'url', 'comments', 'actions'];

  @Input()
  public set operators(value: Operator[]) {
    this.dataSource.setData(value);
    this.triggerChangeDetection();
  }

  @Output()
  removeOperator: EventEmitter<Operator> = new EventEmitter();

  @Output()
  editOperator: EventEmitter<Operator> = new EventEmitter();

  dataSource: ArrayDataSource<Operator> | null;

  constructor(private changeDetectorRef: ChangeDetectorRef) {
    this.dataSource = new ArrayDataSource<Operator>();
  }

  remove(operator: Operator) {
    this.removeOperator.emit(operator);
  }

  edit(operator: Operator) {
    this.editOperator.emit(operator);
  }

  // TODO: check out why this hack is needed
  private triggerChangeDetection() {
    setTimeout(() => this.changeDetectorRef.detectChanges(), 0);
    setTimeout(() => this.changeDetectorRef.detectChanges(), 0);
    setTimeout(() => this.changeDetectorRef.detectChanges(), 0);
  }
}



