import { Component, OnInit, OnDestroy, ChangeDetectionStrategy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ShellSandbox } from '../../shell/shell.sandbox';

@Component({
  selector: 'app-operator',
  templateUrl: './operator-container.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OperatorContainerComponent implements OnInit, OnDestroy {
  operatorId: string;
  private subscription: any;

  constructor(private urlOperator: ActivatedRoute, public sb: ShellSandbox) { }

  ngOnInit() {
    this.subscription = this.urlOperator.params.subscribe(params => {
      this.sb.loadOperators();
      this.operatorId = params['id'];
      if (this.operatorId === 'add') {
        this.operatorId = null;
        this.sb.clearFetchedOperator();
      } else {
        // TODO: figure out ho to remove hack underneath (fetch twice) to fix placeholder bug in mdInput
        this.sb.fetchOperator(this.operatorId);
        this.sb.fetchOperator(this.operatorId);
      }
    });
  }

  checkPristine(pristine: boolean) {
    if (pristine) {
      this.sb.setOperatorDetailPristine();
    } else {
      this.sb.clearOperatorDetailPristine();
    }
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}



