import * as route from '../../actions/data/route';
import { Route } from '../../../trainnetwork/entities/Route';

export function routeReducer(state: Route = new Route(),
  action: route.Actions): Route {
  switch (action.type) {
    case route.ActionTypes.ROUTE_ADD_FETCHED:
      return Object.assign({}, action.payload.route);
    case route.ActionTypes.ROUTE_CLEAR_FETCHED:
      return new Route();
    default:
      return state;
  }
};



