import { Operator } from '../entities/Operator';

export class OperatorModel {
    public id: string;
    public name: string;
    public url: string;
    public comments: string;

    constructor(operator?: Operator) {
        if (operator) {
            this.id = operator.id;
            this.name = operator.name;
            this.url = operator.url;
            this.comments = operator.comments;
        }
    }

    toOperator(): Operator {
        return new Operator(
            this.id,
            this.name,
            this.url,
            this.comments);
    }
}



