import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FlexLayoutModule } from '@angular/flex-layout';
import { StoreModule, combineReducers } from '@ngrx/store';
import { compose } from '@ngrx/core';
import { storeFreeze } from 'ngrx-store-freeze';
import {
  MaterialModule,
  MdTableModule,
  MdButtonModule,
  MdToolbarModule,
  MdSnackBarModule,
  MdInputModule,
  MdChipsModule,
  MdDialogModule
} from '@angular/material';
import 'hammerjs';
import { AppComponent } from './app.component';
import { ShellContainerComponent } from './shell/shell-container/shell-container.component';
import { LoginComponent } from './login/login.component';
import { rootReducer } from '../statemanagement/rootReducer';
import { environment } from '../environments/environment';
import { appState } from '../hmr';
import { AppRoutingModule } from './app-routing.module';
import { MapContainerComponent } from './map/map-container/map-container.component';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import * as Raven from 'raven-js';
import { reset } from '../statemanagement/metareducers/reset-reducer';
import { RouterStoreModule } from '@ngrx/router-store';
import { MenuComponent } from './shell/menu/menu.component';
import { CdkTableModule } from '@angular/cdk';
import { RoutesContainerComponent } from './routes/routes-container/routes-container.component';
import { RoutesListComponent } from './routes/routes-list/routes-list.component';
import { RouteDetailComponent } from './routes/route-detail/route-detail.component';
import { RouteContainerComponent } from './routes/route-container/route-container.component';
import { RoutesService } from '../trainnetwork/services/routes.service';
import { OperatorsContainerComponent } from './operators/operators-container/operators-container.component';
import { OperatorsListComponent } from './operators/operators-list/operators-list.component';
import { OperatorDetailComponent } from './operators/operator-detail/operator-detail.component';
import { OperatorContainerComponent } from './operators/operator-container/operator-container.component';
import { OperatorsService } from '../trainnetwork/services/operators.service';
import { NotificationService } from './services/notification.service';
import { ShellSandbox } from './shell/shell.sandbox';
import { AgmCoreModule } from '@agm/core';
import { MapViewComponent } from './map/map-view/map-view.component';
import { LocalStorageService } from './services/localStorage.service';
import { AuthorizedHttpOptionsService } from 'trainnetwork/services/authorizedHttpOptions.service';
import { AuthenticationService } from '../trainnetwork/services/authentication.service';
import { TimingDialogComponent } from './routes/timing-dialog/timing-dialog.component';

function createComposedReducer() {
  if (environment.production) {
    return compose(reset, combineReducers)(rootReducer);
  }
  return compose(reset, storeFreeze, combineReducers)(rootReducer);
}

export function getComposedReducer(state: any, action: any) {
  return createComposedReducer()(state, action);
}

const imports = [
  StoreModule.provideStore(getComposedReducer, appState),
  RouterStoreModule.connectRouter(),
  BrowserModule,
  FormsModule,
  HttpModule,
  BrowserAnimationsModule,
  FlexLayoutModule,
  MaterialModule,
  AppRoutingModule,
  MdTableModule,
  CdkTableModule,
  MdButtonModule,
  MdToolbarModule,
  MdSnackBarModule,
  MdInputModule,
  MdChipsModule,
  MdDialogModule,
  FormsModule,
  ReactiveFormsModule,
  AgmCoreModule.forRoot({
    apiKey: 'AIzaSyCI-TrT5xbpbakJqRM2i34WtNSq9RrTHFY',
    libraries: ['places']
  })
];
if (!environment.production) {
  // instrument after importing StoreModule and when not in production
  imports.push(StoreDevtoolsModule.instrumentOnlyWithExtension({
    maxAge: 50
  }));
}

Raven
  .config(environment.sentryUrl)
  .install();

export class RavenErrorHandler implements ErrorHandler {
  handleError(err: any): void {
    Raven.captureException(err.originalError || err);
  }
}

@NgModule({
  declarations: [
    AppComponent,
    ShellContainerComponent,
    LoginComponent,
    MapContainerComponent,
    MenuComponent,
    RoutesContainerComponent,
    RoutesListComponent,
    RouteDetailComponent,
    RouteContainerComponent,
    OperatorsContainerComponent,
    OperatorsListComponent,
    OperatorDetailComponent,
    OperatorContainerComponent,
    MapViewComponent,
    TimingDialogComponent
  ],
  entryComponents: [TimingDialogComponent],
  imports: imports,
  providers: [
    { provide: ErrorHandler, useClass: RavenErrorHandler },
    RoutesService,
    OperatorsService,
    NotificationService,
    ShellSandbox,
    LocalStorageService,
    AuthorizedHttpOptionsService,
    AuthenticationService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



