import { Injectable } from '@angular/core';
import { Response, Http } from '@angular/http';
import { environment } from 'environments/environment';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { AuthorizedHttpOptionsService } from 'trainnetwork/services/authorizedHttpOptions.service';

@Injectable()
export abstract class BaseService<T> {
  protected abstract get apiSuffix(): string;
  private apiUrl: string;

  constructor(private http: Http, private authorizedHttpOptionsService: AuthorizedHttpOptionsService) {
    this.apiUrl = `${environment.apiUrl}${this.apiSuffix}`;
  }

  add(item: T): Observable<Response> {
    return this.http.post(this.apiUrl, item, this.authorizedHttpOptionsService.options);
  }

  update(item: T): Observable<Response> {
    return this.http.put(this.apiUrl, item, this.authorizedHttpOptionsService.options);
  }

  remove(id: string): Observable<Response> {
    return this.http.delete(`${this.apiUrl}/${id}`, this.authorizedHttpOptionsService.options);
  }

  fetchAll(): Observable<Array<T>> {
    return this.http.get(this.apiUrl, this.authorizedHttpOptionsService.options)
      .map((res: Response) => res.json());
  }

  fetch(id: string): Observable<T> {
    return this.http.get(`${this.apiUrl}/${id}`, this.authorizedHttpOptionsService.options)
      .map((res: Response) => res.json());
  }
}



