import * as operatorDetailPristine from '../../actions/containers/operator-detail';
import { PristineContainerState } from '../../state/ContainersState';

const initialState: PristineContainerState = {
    isPristine: true
};

export function operatorDetailPristineReducer(state: PristineContainerState = initialState,
    action: operatorDetailPristine.Actions): PristineContainerState {
    switch (action.type) {
        case operatorDetailPristine.ActionTypes.CLEAR_PRESTINE:
            return {
                isPristine: false
            };
        case operatorDetailPristine.ActionTypes.SET_PRESTINE:
            return {
                isPristine: true
            };
        default:
            return state;
    }
};



