import {
  Component,
  OnInit,
  Input,
  ChangeDetectionStrategy,
  EventEmitter,
  Output,
  ViewChild,
  ElementRef,
  NgZone,
  ChangeDetectorRef
} from '@angular/core';
import { Route } from '../../../trainnetwork/entities/Route';
import { RouteModel } from '../../../trainnetwork/model/RouteModel';
import { FormControl, Validators, NgForm, FormGroup } from '@angular/forms';
import { ArrayDataSource } from '../../ArrayDataSource';
import * as _ from 'lodash';
import { MapTypeId } from '@agm/core/services/google-maps-types';
import { LatLngBoundsLiteral, MapsAPILoader } from '@agm/core';
import { Operator } from '../../../trainnetwork/entities/Operator';
import { Timing } from '../../../trainnetwork/entities/Timing';
import { TimingModel } from '../../../trainnetwork/model/TimingModel';
import { TimingDataModel } from '../../../trainnetwork/model/TimingDataModel';
import { MdDialog } from '@angular/material';
import { TimingDialogComponent } from '../timing-dialog/timing-dialog.component';
import { TimingDialogModel } from '../TimingDialogModel';

// TODO: import googlemaps typings https://stackoverflow.com/questions/42394697/angular2-cannot-find-namespace-google
// TODO: check out changedetection
// TODO: pull out dumb components
declare var google: any;

@Component({
  selector: 'app-route-detail',
  templateUrl: './route-detail.component.html',
  changeDetection: ChangeDetectionStrategy.Default
})
export class RouteDetailComponent implements OnInit {
  routeModel: RouteModel;
  intitialRouteModelJson: string;
  availableRoutes: Route[];
  availableOperators: Operator[];
  selectedAvailableRouteId: string;
  selectedAvailableOperatorId: string;
  fitBounds: LatLngBoundsLiteral;

  detailFormGroup: FormGroup;

  nameFormControl = new FormControl('', [Validators.required]);
  viaFormControl = new FormControl();
  operatorFormControl = new FormControl();
  latitudeFormControl = new FormControl('', [Validators.required]);
  longitudeFormControl = new FormControl('', [Validators.required]);

  pinMode = false;

  displayedColumnsRoute = ['terminal', 'type', 'actions'];
  displayedColumnsTiming = ['operator', 'To', 'From', 'actions'];

  @ViewChild('search')
  public searchElementRef: ElementRef;

  private isPristine = true;
  private _routes: Route[];

  @Input()
  public set routes(value: Route[]) {
    this._routes = value;
    this.setAvailableRoutes();
  }

  @Input()
  public set route(value: Route) {
    this.routeModel = new RouteModel(value);
    this.setAvailableRoutes();
    this.setAvailableOperators();
    this.intitialRouteModelJson = JSON.stringify(this.routeModel);
    this.zoomToFit();
  }

  private _operators: Operator[];

  @Input()
  public set operators(value: Operator[]) {
    this._operators = value;
    this.setAvailableOperators();
  }

  dataSourceVias: ArrayDataSource<Route> | null;
  dataSourceTimings: ArrayDataSource<TimingModel> | null;

  @Output()
  cancel: EventEmitter<any> = new EventEmitter;

  @Output()
  save: EventEmitter<Route> = new EventEmitter;

  @Output()
  pristine: EventEmitter<boolean> = new EventEmitter;

  zoomToFit() {
    this.zoomToPoint(this.routeModel.terminal.coordinates.latitude, this.routeModel.terminal.coordinates.longitude);
  }

  zoomToPoint(latitude: number, longitude: number) {
    if (latitude === 0 && longitude === 0) {
      this.fitBounds = <LatLngBoundsLiteral>{
        north: 65.4765075626,
        east: 52.734375,
        south: 35.8178131587,
        west: -10.8984375
      };
    } else {
      this.fitBounds = <LatLngBoundsLiteral>{
        north: latitude + 0.5,
        east: longitude + 0.7,
        south: latitude - 0.5,
        west: longitude - 0.7
      };
    }
  }

  constructor(
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone,
    private changeDetectorRef: ChangeDetectorRef,
    private dialog: MdDialog
  ) {
    this.dataSourceVias = new ArrayDataSource<Route>();
    this.dataSourceTimings = new ArrayDataSource<TimingModel>();
  }

  // TODO: check out why this hack is needed
  private triggerChangeDetection() {
    setTimeout(() => this.changeDetectorRef.detectChanges(), 0);
    setTimeout(() => this.changeDetectorRef.detectChanges(), 0);
    setTimeout(() => this.changeDetectorRef.detectChanges(), 0);
  }

  private setAvailableRoutes() {
    this.selectedAvailableRouteId = null;
    if (this.routeModel && this._routes) {
      this.availableRoutes = this._routes.filter(r => r.id !== this.routeModel.id && !_.includes(this.routeModel.viaIds, r.id));
      const currentRoutes = this._routes.filter(r => _.includes(this.routeModel.viaIds, r.id));
      this.dataSourceVias.setData(currentRoutes);
      this.triggerChangeDetection();
    }
  }

  private setAvailableOperators() {
    this.selectedAvailableOperatorId = null;
    if (this.routeModel && this._operators) {
      const operatorIds: string[] = [];
      this.routeModel.timings.forEach(t => operatorIds.push(t.operatorId));
      this.availableOperators = this._operators.filter(o => !_.includes(operatorIds, o.id));
      this.dataSourceTimings.setData(this.routeModel.timings);
      this.triggerChangeDetection();
    }
  }

  ngOnInit() {
    this.initializeFormGroup();
    this.initializeCheckModel();
    this.initializeMaps();
  }

  private initializeMaps() {
    this.mapsAPILoader.load().then(() => {
      const autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
        types: ['(cities)']
      });
      google.maps.event.addListener(autocomplete, 'place_changed', () => {
        this.ngZone.run(() => {
          // get the place result
          const place: any = autocomplete.getPlace();

          // verify result
          if (place.geometry === undefined || place.geometry === null) {
            return;
          }

          // set latitude, longitude and zoom
          this.zoomToPoint(place.geometry.location.lat(), place.geometry.location.lng());
          this.changeDetectorRef.detectChanges();
        });
      });
    });
  }

  private initializeCheckModel() {
    const changeSignaler = (value: any) => this.checkModel();

    this.nameFormControl.valueChanges.subscribe(changeSignaler);
    this.viaFormControl.valueChanges.subscribe(changeSignaler);
    this.operatorFormControl.valueChanges.subscribe(changeSignaler);
    this.latitudeFormControl.valueChanges.subscribe(changeSignaler);
    this.longitudeFormControl.valueChanges.subscribe(changeSignaler);
  }

  private initializeFormGroup() {
    this.detailFormGroup = new FormGroup({});
    this.detailFormGroup.addControl('name', this.nameFormControl);
    this.detailFormGroup.addControl('via', this.viaFormControl);
    this.detailFormGroup.addControl('operator', this.operatorFormControl);
    this.detailFormGroup.addControl('latitude', this.latitudeFormControl);
    this.detailFormGroup.addControl('longitude', this.longitudeFormControl);
  }

  getTitle(routeModel: RouteModel): string {
    if (routeModel.id) {
      return 'Edit route';
    }
    return 'Add route';
  }

  cancelDetail() {
    this.cancel.emit(null);
  }

  saveDetail() {
    this.save.emit(this.routeModel.toRoute());
  }

  checkModel() {
    const currentPristine = JSON.stringify(this.routeModel) === this.intitialRouteModelJson;
    if (this.isPristine !== currentPristine) {
      this.isPristine = currentPristine;
      this.pristine.emit(currentPristine);
    }
  }

  removeViaRoute(viaRoute: Route) {
    this.routeModel.viaIds = this.routeModel.viaIds.filter(id => viaRoute.id !== id);
    this.setAvailableRoutes();
    this.checkModel();
  }

  addViaRoute() {
    if (this.routeModel.viaIds === null) {
      this.routeModel.viaIds = [];
    }
    if (this.selectedAvailableRouteId) {
      this.routeModel.viaIds = [...this.routeModel.viaIds, this.selectedAvailableRouteId];
    }
    this.setAvailableRoutes();
    this.checkModel();
  }

  clickedMap(event: any) {
    if (this.pinMode) {
      this.routeModel.terminal.coordinates.latitude = event.coords.lat;
      this.routeModel.terminal.coordinates.longitude = event.coords.lng;
      this.pinMode = false;
    }
  }

  getOperatorName(timing: TimingModel) {
    const operator = this._operators.find(o => o.id === timing.operatorId);
    return operator === null ? 'Unknown' : operator.name;
  }

  removeTiming(timing: TimingModel) {
    const index = _.findIndex(this.routeModel.timings, _.pick(timing, 'operatorId'));
    if (index !== -1) {
      this.routeModel.timings.splice(index, 1);
      this.setAvailableOperators();
      this.checkModel();
    }
  }

  editTiming(operatorName: string, timing: TimingModel, add?: boolean) {
    const dialogRef = this.dialog.open(TimingDialogComponent, {
      disableClose: true,
      data: new TimingDialogModel(
        this.routeModel.terminal.name,
        operatorName,
        add,
        timing
      )
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const timingDialogModel = <TimingDialogModel>result;
        if (timingDialogModel.add ||
          JSON.stringify(timingDialogModel.originalTimingModel) !== JSON.stringify(timingDialogModel.modifiedTimingModel)) {
          const index = _.findIndex(this.routeModel.timings, _.pick(timingDialogModel.modifiedTimingModel, 'operatorId'));
          if (index !== -1) {
            this.routeModel.timings.splice(index, 1, timingDialogModel.modifiedTimingModel);
          } else {
            this.routeModel.timings.push(timingDialogModel.modifiedTimingModel);
          }
          this.setAvailableOperators();
          this.checkModel();
        }
      }
    });
  }

  getTimingDataToDisplay(timingData: TimingDataModel) {
    if (timingData === null) {
      return null;
    } else {
      if (this.hasTimingData(timingData)) {
        return `${timingData.departure === null || timingData.departure.trim() === '' ? 'Unknown' : timingData.departure}\
\n${timingData.closing === null || timingData.closing.trim() === '' ? 'Unknown' : timingData.closing}\
\n${timingData.pickup === null || timingData.pickup.trim() === '' ? 'Unknown' : timingData.pickup}\
\n${timingData.profile === null || timingData.profile.trim() === '' ? 'Unknown' : timingData.profile}`;
      } else {
        return '';
      }
    }
  }

  private hasTimingData(timingData: TimingDataModel) {
    if (timingData.departure !== null && timingData.departure.trim() !== '') {
      return true;
    }
    if (timingData.closing !== null && timingData.closing.trim() !== '') {
      return true;
    }
    if (timingData.pickup !== null && timingData.pickup.trim() !== '') {
      return true;
    }
    if (timingData.profile !== null && timingData.profile.trim() !== '') {
      return true;
    }
    return false;
  }

  addTiming() {
    if (this.selectedAvailableOperatorId) {
      const timingModel = new TimingModel();
      timingModel.operatorId = this.selectedAvailableOperatorId;
      this.editTiming(this.getOperatorName(timingModel), timingModel, true);
    }
  }
}



