import * as operator from '../../actions/data/operator';
import { Operator } from '../../../trainnetwork/entities/Operator';

export function operatorsReducer(state: Array<Operator> = [],
  action: operator.Actions): Array<Operator> {
  switch (action.type) {
    case operator.ActionTypes.OPERATORS_ADD:
      return [...state, action.payload.operator];
    case operator.ActionTypes.OPERATORS_SET_ALL:
      return [...action.payload.operators];
    case operator.ActionTypes.OPERATORS_REMOVE:
      return state.filter(item => item.id !== action.payload.id);
    case operator.ActionTypes.OPERATORS_UPDATE:
      return state.map(item => item.id === action.payload.id ? Object.assign({}, action.payload.operator) : item);
    default:
      return state;
  }
};



