import { Injectable } from '@angular/core';
import { LocalStorageService } from '../../app/services/localStorage.service';
import { RequestOptionsArgs, Headers, RequestOptions } from '@angular/http';


@Injectable()
export class AuthorizedHttpOptionsService {
  constructor(private localStorageService: LocalStorageService) {
  }

  public get options(): RequestOptionsArgs {
    const headers = new Headers({
      authorization: `Bearer ${this.localStorageService.token}`
    });
    return new RequestOptions({ headers: headers });
  }

  setToken(token: string) {
    this.localStorageService.token = token;
  }
}



