import { Injectable, OnDestroy } from '@angular/core';
import { CanDeactivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { OperatorDetailComponent } from '../operators/operator-detail/operator-detail.component';
import { Observable } from 'rxjs/Observable';
import { ShellSandbox } from '../shell/shell.sandbox';
import { unsavedChangesMessage } from '../messages';

@Injectable()
export class CancelOperatorDetailGuard implements CanDeactivate<OperatorDetailComponent>, OnDestroy {
    private isOperatorDetailPrisitine: boolean;
    private isOperatorDetailPristineSubscription: any;

    constructor(private sb: ShellSandbox) {
        sb.operatorDetailPristine$.subscribe(pristine => this.isOperatorDetailPrisitine = pristine.isPristine);
    }

    ngOnDestroy() {
        this.isOperatorDetailPristineSubscription.unsubscribe();
    }

    canDeactivate(
        component: OperatorDetailComponent,
        currentOperator: ActivatedRouteSnapshot,
        currentState: RouterStateSnapshot,
        nextState: RouterStateSnapshot
    ): Observable<boolean> | Promise<boolean> | boolean {
        if (!this.isOperatorDetailPrisitine) {
            const result = confirm(unsavedChangesMessage);
            if (result) {
                this.sb.setOperatorDetailPristine();
            }
            return result;
        }
        return true;
    }
}



