import { type } from '../../../util/util';
import { Action } from '@ngrx/store';
import { Route } from '../../../trainnetwork/entities/Route';

export const ActionTypes = {
  ROUTES_ADD: type<'DATA_ROUTES_ADD'>('DATA_ROUTES_ADD'),
  ROUTES_REMOVE: type<'DATA_ROUTES_REMOVE'>('DATA_ROUTES_REMOVE'),
  ROUTES_UPDATE: type<'DATA_ROUTES_UPDATE'>('DATA_ROUTES_UPDATE'),
  ROUTES_SET_ALL: type<'DATA_ROUTES_ADD_ALL'>('DATA_ROUTES_ADD_ALL'),
  ROUTE_ADD_FETCHED: type<'DATA_ROUTE_ADD_FETCHED'>('DATA_ROUTE_ADD_FETCHED'),
  ROUTE_CLEAR_FETCHED: type<'DATA_ROUTE_CLEAR_FETCHED'>('DATA_ROUTE_CLEAR_FETCHED')
};

export class AddFetchedRoute implements Action {
  type = ActionTypes.ROUTE_ADD_FETCHED;
  payload: Readonly<{ route: Route }>;

  constructor(route: Route) {
    this.payload = { route };
  }
}

export class ClearFetchedRoute implements Action {
  type = ActionTypes.ROUTE_CLEAR_FETCHED;
}

export class AddRoute implements Action {
  type = ActionTypes.ROUTES_ADD;
  payload: Readonly<{ route: Route }>;

  constructor(route: Route) {
    this.payload = { route };
  }
}

export class RemoveRoute implements Action {
  type = ActionTypes.ROUTES_REMOVE;
  payload: Readonly<{ id: string }>;

  constructor(id: string) {
    this.payload = { id };
  }
}

export class UpdateRoute implements Action {
  type = ActionTypes.ROUTES_UPDATE;
  payload: Readonly<{ id: string, route: Route }>;

  constructor(id: string, route: Route) {
    this.payload = { id, route };
  }
}

export class SetAllRoutes implements Action {
  type = ActionTypes.ROUTES_SET_ALL;
  payload: Readonly<{ routes: Route[] }>;

  constructor(routes: Route[]) {
    this.payload = { routes };
  }
}

export type Actions =
  AddRoute
  | RemoveRoute
  | UpdateRoute
  | SetAllRoutes
  | AddFetchedRoute
  | ClearFetchedRoute;



