import { Component, OnInit, OnDestroy, ChangeDetectionStrategy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ShellSandbox } from '../../shell/shell.sandbox';

@Component({
  selector: 'app-route',
  templateUrl: './route-container.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RouteContainerComponent implements OnInit, OnDestroy {
  routeId: string;
  private subscription: any;

  constructor(private urlRoute: ActivatedRoute, public sb: ShellSandbox) { }

  ngOnInit() {
    this.subscription = this.urlRoute.params.subscribe(params => {
      this.sb.loadRoutes();
      this.sb.loadOperators();
      this.routeId = params['id'];
      if (this.routeId === 'add') {
        this.routeId = null;
        this.sb.clearFetchedRoute();
      } else {
        // TODO: figure out ho to remove hack underneath (fetch twice) to fix placeholder bug in mdInput
        this.sb.fetchRoute(this.routeId);
        this.sb.fetchRoute(this.routeId);
      }
    });
  }

  checkPristine(pristine: boolean) {
    if (pristine) {
      this.sb.setRouteDetailPristine();
    } else {
      this.sb.clearRouteDetailPristine();
    }
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}



