import { combineReducers } from '@ngrx/store';
import { authenticationReducer } from './reducers/data/authentication.reducer';
import { routesReducer } from './reducers/data/routes.reducer';
import { routerReducer } from '@ngrx/router-store';
import { routeReducer } from './reducers/data/route.reducer';
import { routeDetailPristineReducer } from './reducers/containers/route-detail.reducer';
import { operatorDetailPristineReducer } from './reducers/containers/operator-detail.reducer';
import { operatorsReducer } from './reducers/data/operators.reducer';
import { operatorReducer } from 'statemanagement/reducers/data/operator.reducer';

const dataReducers = combineReducers({
    authentication: authenticationReducer,
    routes: routesReducer,
    route: routeReducer,
    operators: operatorsReducer,
    operator: operatorReducer
});

const containerReducers = combineReducers({
    routeDetailPristine: routeDetailPristineReducer,
    operatorDetailPristine: operatorDetailPristineReducer
});

export const rootReducer = {
    data: dataReducers,
    containers: containerReducers,
    router: routerReducer
};



