import { type } from '../../../util/util';
import { Action } from '@ngrx/store';
import { Operator } from '../../../trainnetwork/entities/Operator';

export const ActionTypes = {
  OPERATORS_ADD: type<'DATA_OPERATORS_ADD'>('DATA_OPERATORS_ADD'),
  OPERATORS_REMOVE: type<'DATA_OPERATORS_REMOVE'>('DATA_OPERATORS_REMOVE'),
  OPERATORS_UPDATE: type<'DATA_OPERATORS_UPDATE'>('DATA_OPERATORS_UPDATE'),
  OPERATORS_SET_ALL: type<'DATA_OPERATORS_ADD_ALL'>('DATA_OPERATORS_ADD_ALL'),
  OPERATOR_ADD_FETCHED: type<'DATA_OPERATOR_ADD_FETCHED'>('DATA_OPERATOR_ADD_FETCHED'),
  OPERATOR_CLEAR_FETCHED: type<'DATA_OPERATOR_CLEAR_FETCHED'>('DATA_OPERATOR_CLEAR_FETCHED')
};

export class AddFetchedOperator implements Action {
  type = ActionTypes.OPERATOR_ADD_FETCHED;
  payload: Readonly<{ operator: Operator }>;

  constructor(operator: Operator) {
    this.payload = { operator };
  }
}

export class ClearFetchedOperator implements Action {
  type = ActionTypes.OPERATOR_CLEAR_FETCHED;
}

export class AddOperator implements Action {
  type = ActionTypes.OPERATORS_ADD;
  payload: Readonly<{ operator: Operator }>;

  constructor(operator: Operator) {
    this.payload = { operator };
  }
}

export class RemoveOperator implements Action {
  type = ActionTypes.OPERATORS_REMOVE;
  payload: Readonly<{ id: string }>;

  constructor(id: string) {
    this.payload = { id };
  }
}

export class UpdateOperator implements Action {
  type = ActionTypes.OPERATORS_UPDATE;
  payload: Readonly<{ id: string, operator: Operator }>;

  constructor(id: string, operator: Operator) {
    this.payload = { id, operator };
  }
}

export class SetAllOperators implements Action {
  type = ActionTypes.OPERATORS_SET_ALL;
  payload: Readonly<{ operators: Operator[] }>;

  constructor(operators: Operator[]) {
    this.payload = { operators };
  }
}

export type Actions =
  AddOperator
  | RemoveOperator
  | UpdateOperator
  | SetAllOperators
  | AddFetchedOperator
  | ClearFetchedOperator;



