import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MapContainerComponent } from './map/map-container/map-container.component';
import { RoutesContainerComponent } from './routes/routes-container/routes-container.component';
import { RouteUrls } from './RouteUrls';
import { RouteContainerComponent } from './routes/route-container/route-container.component';
import { CancelRouteDetailGuard } from './guards/cancel-route-detail.guard';
import { OperatorsContainerComponent } from './operators/operators-container/operators-container.component';
import { OperatorContainerComponent } from './operators/operator-container/operator-container.component';
import { CancelOperatorDetailGuard } from './guards/cancel-operator-detail.guard';

const routes: Routes = [
  {
    path: RouteUrls.URL_MAP,
    component: MapContainerComponent
  },
  {
    path: RouteUrls.URL_ROUTES,
    component: RoutesContainerComponent
  },
  {
    path: RouteUrls.URL_ROUTES + '/:id',
    component: RouteContainerComponent,
    canDeactivate: [CancelRouteDetailGuard]
  },
  {
    path: RouteUrls.URL_ROUTE_ADD,
    component: RouteContainerComponent
  },
  {
    path: RouteUrls.URL_OPERATORS,
    component: OperatorsContainerComponent
  },
  {
    path: RouteUrls.URL_OPERATORS + '/:id',
    component: OperatorContainerComponent,
    canDeactivate: [CancelOperatorDetailGuard]
  },
  {
    path: RouteUrls.URL_OPERATOR_ADD,
    component: OperatorContainerComponent
  },
  {
    path: '',
    redirectTo: RouteUrls.URL_MAP,
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [
    CancelRouteDetailGuard,
    CancelOperatorDetailGuard
  ]
})
export class AppRoutingModule { }



