import { Injectable } from '@angular/core';
import { Operator } from '../entities/Operator';
import { BaseService } from './base.service';

@Injectable()
export class OperatorsService extends BaseService<Operator> {
  protected get apiSuffix(): string {
    return '/operators';
  };
}



