import { type } from '../../../util/util';
import { Action } from '@ngrx/store';

export const ActionTypes = {
    SET_PRESTINE: type<'CONTAINER_OPERATORDETAIL_SET_PRISTINE'>('CONTAINER_OPERATORDETAIL_SET_PRISTINE'),
    CLEAR_PRESTINE: type<'CONTAINER_OPERATORDETAIL_CLEAR_PRISTINE'>('CONTAINER_OPERATORDETAIL_CLEAR_PRISTINE'),
};

export class SetOperatorDetailPristine implements Action {
    type = ActionTypes.SET_PRESTINE;

    public constructor() {
    }
}

export class ClearOperatorDetailPristine implements Action {
    type = ActionTypes.CLEAR_PRESTINE;

    public constructor() {
    }
}

export type Actions = SetOperatorDetailPristine | ClearOperatorDetailPristine;



