import { Injectable } from '@angular/core';
import { Response, Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { TokenData } from '../entities/TokenData';
import { AuthorizedHttpOptionsService } from 'trainnetwork/services/authorizedHttpOptions.service';
import { environment } from 'environments/environment';

const AUTHENTICATION_API_SUFFIX = '/authentication';

@Injectable()
export class AuthenticationService {
    readonly authenticationApiUrl = `${environment.apiUrl}${AUTHENTICATION_API_SUFFIX}`;

    constructor(private http: Http, private authorizedHttpOptionsService: AuthorizedHttpOptionsService) {
    }

    login(tokenData: TokenData): Observable<boolean> {
        this.authorizedHttpOptionsService.setToken(tokenData.token);
        return this.http.post(this.authenticationApiUrl, tokenData, this.authorizedHttpOptionsService.options)
            .map((res: Response) => res.json());
    }
}



