import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { ShellSandbox } from '../../shell/shell.sandbox';
import { Operator } from '../../../trainnetwork/entities/Operator';

@Component({
  selector: 'app-operators',
  templateUrl: './operators-container.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OperatorsContainerComponent implements OnInit {
  constructor(public sb: ShellSandbox) { }

  ngOnInit() {
    this.sb.loadOperators();
  }

  removeOperator(operator: Operator) {
    if (confirm(`Are you sure you want to remove operator "${operator.name}"?`)) {
      this.sb.removeOperator(operator.id);
    }
  }

  editOperator(operator: Operator) {
    this.sb.gotoEditOperator(operator.id);
  }

  createOrUpdateOperator(operator: Operator) {
    this.sb.addOrUpdateOperator(operator);
  }
}



