import { Component, Input } from '@angular/core';
import { Route } from '../../../trainnetwork/entities/Route';
import { LatLngBoundsLiteral } from '@agm/core';
import { Line } from './Line';
import { Location } from './Location';
import { MapTransformationHelper } from './MapTransformationHelper';
import { Operator } from '../../../trainnetwork/entities/Operator';
import { Timing } from '../../../trainnetwork/entities/Timing';
import { TimingData } from '../../../trainnetwork/entities/TimingData';

@Component({
  selector: 'app-map-view',
  templateUrl: './map-view.component.html',
})
export class MapViewComponent {
  locations: Location[] = [];
  fitBounds: LatLngBoundsLiteral;
  lines: Line[];
  private _routes: Route[];
  private _operators: Operator[];


  @Input()
  public set routes(value: Route[]) {
    this._routes = value;
    this.initData();
  }

  @Input()
  public set operators(value: Operator[]) {
    this._operators = value;
    this.initData();
  }

  private initData() {
    if (this._routes && this._operators) {
      this.locations = MapTransformationHelper.getLocations(this._routes);
      this.lines = MapTransformationHelper.getLines(this._routes);
      this.fitBounds = MapTransformationHelper.getBounds(this.locations);
    }
  }

  zoomToFit() {
    // some trickery to trigger the databinding again, DO NOT USE OnPush databinding!
    const bounds = Object.assign({}, this.fitBounds);
    this.fitBounds = <LatLngBoundsLiteral>{ north: 0, east: 0, south: 0, west: 0 };
    this.fitBounds = bounds;
  }

  private getOperator(operatorId: string): Operator {
    const operator = this._operators.find(o => o.id === operatorId);
    if (operator) {
      return operator;
    } else {
      return null;
    }
  }

  getOperatorName(timing: Timing): string {
    const operator = this.getOperator(timing.operatorId);
    if (operator) {
      return operator.name;
    } else {
      return null;
    }
  }

  hasOperatorUrl(timing: Timing) {
    return this.getOperatorUrl(timing) !== null;
  }

  getOperatorUrl(timing: Timing): string {
    const operator = this.getOperator(timing.operatorId);
    if (operator) {
      const url = operator.url;
      if (url && url.trim() !== '') {
        return url;
      } else {
        return null;
      }
    } else {
      return null;
    }
  }

  hasOperatorComments(timing: Timing) {
    return this.getOperatorComments(timing) !== null;
  }

  getOperatorComments(timing: Timing): string {
    const operator = this.getOperator(timing.operatorId);
    if (operator) {
      const comments = operator.comments;
      if (comments && comments.trim() !== '') {
        return comments;
      } else {
        return null;
      }
    } else {
      return null;
    }
  }

  hasTimingData(timingData: TimingData) {
    return timingData !== null
      && timingData.departure !== null
      && timingData.closing !== null
      && timingData.pickup !== null
      && timingData.profile !== null;
  }

  getFormattedTimingData(prefix: string, timingData: TimingData) {
    return `${prefix}\n\
 departure ${timingData.departure}\n\
   closing ${timingData.closing}\n\
    pickup ${timingData.pickup}\n\
   profile ${timingData.profile}`;
  }
}



